<template>
  <div class="container">
    <h1 class="title">Search Page</h1>
    <div class="search-bar">
      <input
        v-model="searchQuery"
        type="text"
        placeholder="Search for recipes..."
        @keyup.enter="searchRecipes"
      />
      <button @click="searchRecipes">Search</button>
    </div>
    <div class="filter-options">
      <div
        v-for="(filter, index) in filters"
        :key="index"
        class="filter-option"
      >
        <div class="dropdown">
          <div class="filter-button" @click="toggleDropdown(index)">
            <span class="emoji" v-html="filter.emoji"></span>
            <span class="filter-label">{{ filter.label }}</span>
          </div>
          <div class="dropdown-options" v-show="filter.isOpen">
            <div v-for="option in filter.options" :key="option">
              <input
                type="checkbox"
                :id="`${filter.label}-${option}`"
                :value="option"
                v-model="filter.selectedOptions"
              />
              <label :for="`${filter.label}-${option}`">{{ option }}</label>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="search-results" v-if="recipes.length > 0">
      <h2>Search Results:</h2>
      <div v-for="recipe in recipes" :key="recipe.id" class="recipe-preview">
        <img
          :src="recipe.image"
          :alt="recipe.title"
          @click="viewRecipe(recipe.id)"
        />
        <h3>{{ recipe.title }}</h3>
        <div>Preparation Time: {{ recipe.readyInMinutes }} minutes</div>
        <div>Popularity: {{ recipe.popularity }} likes</div>
      </div>
    </div>
    <div v-else class="no-results">
      <p>No results found.</p>
    </div>
  </div>
</template>

<script>
import diets from "@/assets/diets.js";
import cuisines from "@/assets/cuisines.js";
import intolerances from "@/assets/intolerances.js";

export default {
  data() {
    return {
      searchQuery: "",
      selectedFilters: [[], [], []],
      filters: [
        {
          emoji: "🍽️",
          label: "Cuisine",
          options: cuisines,
          isOpen: false,
          selectedOptions: []
        },
        {
          emoji: "🥦",
          label: "Diet",
          options: diets,
          isOpen: false,
          selectedOptions: []
        },
        {
          emoji: "🤒",
          label: "Intolerance",
          options: intolerances,
          isOpen: false,
          selectedOptions: []
        }
      ],
      recipes: []
    };
  },
  methods: {
    async searchRecipes() {
      const response = await this.axios.get("/api/recipes", {
        params: {
          searchQuery: this.searchQuery,
          cuisine: this.selectedFilters[0],
          diet: this.selectedFilters[1],
          intolerance: this.selectedFilters[2]
        }
      });

      if (response.status === 200) {
        this.recipes = response.data;
      } else {
        this.recipes = [];
      }
    },

    viewRecipe(recipeId) {
      this.$router.push(`/recipe/${recipeId}`);
    },

    toggleDropdown(index) {
      this.filters[index].isOpen = !this.filters[index].isOpen;
    }
  }
};
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.search-bar {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.search-bar input {
  width: 400px;
  height: 40px;
  font-size: 18px;
  padding: 5px;
}

.search-bar button {
  height: 40px;
  font-size: 18px;
  margin-left: 10px;
}

.filter-options {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.filter-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin: 0 10px;
}

.filter-button {
  display: flex;
  align-items: center;
}

.emoji {
  font-size: 24px;
  margin-right: 5px;
}

.filter-label {
  font-weight: bold;
}

.dropdown {
  position: relative;
}

.dropdown-options {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.dropdown-options div {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.recipe-preview {
  margin-bottom: 20px;
}

.recipe-preview img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  cursor: pointer;
}

.no-results {
  margin-top: 20px;
  font-style: italic;
}
</style>
